import React from 'react';
import MovieContext from '../../context/MovieContext'
import MoviePreviewSlider from '../../shared/components/Sliders/MoviePreviewSlider'

import './MainPage.scss'

export default function MoviesPage() {

  let {sliderMovies, isSliderMoviesLoading, sliderTV, isSliderTVLoading} = React.useContext(MovieContext)
  return(
    <>
    <h2 className='main-slider__title'>Сегодняшние тренды фильмов</h2>
    <MoviePreviewSlider items={sliderMovies} 
                        isLoading={isSliderMoviesLoading}
                        swiperClass={"popular-films-slider"}
                        swiperSlideClass={"slide-preview-films"}
                        type={'movie'}
                        />
    <h2 className='main-slider__title'>Сегодняшние тренды сериалов</h2>
    <MoviePreviewSlider items={sliderTV} 
                        isLoading={isSliderTVLoading}
                        swiperClass={"popular-films-slider"}
                        swiperSlideClass={"slide-preview-films"}
                        type={'tv'}
                        />

    </>
  )
}
